import { useState } from 'react';
import { 
  EditorState, 
  RichUtils, 
  SelectionState,
  Modifier
} from 'draft-js';

export const useLinkHandlers = (editorState, onEditorStateChange) => {
  const [linkDialogOpen, setLinkDialogOpen] = useState(false);

  // èŽ·å–å½“å‰é€‰æ‹©çš„é“¾æŽ¥å®žä½“
  const getCurrentLinkEntity = (editorState) => {
    const selection = editorState.getSelection();
    const contentState = editorState.getCurrentContent();
    const startKey = selection.getStartKey();
    const startOffset = selection.getStartOffset();
    const blockWithLinkAtBeginning = contentState.getBlockForKey(startKey);
    const linkKey = blockWithLinkAtBeginning.getEntityAt(startOffset);

    if (linkKey) {
      const linkInstance = contentState.getEntity(linkKey);
      if (linkInstance.getType() === 'LINK') {
        return linkKey;
      }
    }
    return null;
  };

  // æ’å…¥æ–°é“¾æŽ¥
  const insertLink = (url, text) => {
    const selection = editorState.getSelection();
    const contentState = editorState.getCurrentContent();
    
    // åˆ›å»ºé“¾æŽ¥å®žä½“
    const contentStateWithEntity = contentState.createEntity(
      'LINK',
      'MUTABLE',
      { url, text }
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    
    let newContentState;
    let newEditorState;
    
    if (selection.isCollapsed()) {
      // å¦‚æžœæ²¡æœ‰é€‰ä¸­æ–‡æœ¬ï¼Œæ’å…¥æ–°æ–‡æœ¬
      const textToInsert = text || url;
      newContentState = Modifier.insertText(
        contentStateWithEntity,
        selection,
        textToInsert,
        null,
        entityKey
      );
    } else {
      // å¦‚æžœæœ‰é€‰ä¸­æ–‡æœ¬ï¼Œä¸ºé€‰ä¸­çš„æ–‡æœ¬æ·»åŠ é“¾æŽ¥
      newContentState = Modifier.applyEntity(
        contentStateWithEntity,
        selection,
        entityKey
      );
    }
    
    newEditorState = EditorState.push(
      editorState,
      newContentState,
      'apply-entity'
    );
    
    // ç§»åŠ¨å…‰æ ‡åˆ°é“¾æŽ¥æ–‡æœ¬çš„æœ«å°¾
    if (selection.isCollapsed()) {
      const textToInsert = text || url;
      const newSelection = selection.merge({
        anchorOffset: selection.getAnchorOffset() + textToInsert.length,
        focusOffset: selection.getFocusOffset() + textToInsert.length,
      });
      newEditorState = EditorState.forceSelection(newEditorState, newSelection);
    }
    
    onEditorStateChange(newEditorState);
  };

  // æ›´æ–°çŽ°æœ‰é“¾æŽ¥
  const updateLink = (url, text) => {
    const selection = editorState.getSelection();
    const contentState = editorState.getCurrentContent();
    const currentEntity = getCurrentLinkEntity(editorState);
    
    if (currentEntity) {
      // æ›´æ–°å®žä½“æ•°æ®
      const newContentState = contentState.mergeEntityData(
        currentEntity,
        { url, text }
      );
      
      const newEditorState = EditorState.push(
        editorState,
        newContentState,
        'apply-entity'
      );
      
      onEditorStateChange(newEditorState);
    }
  };

  // ç§»é™¤é“¾æŽ¥
  const removeLink = () => {
    const selection = editorState.getSelection();
    const currentEntity = getCurrentLinkEntity(editorState);
    
    if (currentEntity) {
      // æ‰¾åˆ°æ•´ä¸ªé“¾æŽ¥çš„èŒƒå›´
      const contentState = editorState.getCurrentContent();
      const startKey = selection.getStartKey();
      const block = contentState.getBlockForKey(startKey);
      
      let linkStart = null;
      let linkEnd = null;
      
      // æ‰¾åˆ°é“¾æŽ¥çš„å¼€å§‹å’Œç»“æŸä½ç½®
      block.findEntityRanges(
        (character) => {
          const entityKey = character.getEntity();
          return entityKey === currentEntity;
        },
        (start, end) => {
          linkStart = start;
          linkEnd = end;
        }
      );
      
      if (linkStart !== null && linkEnd !== null) {
        const linkSelection = SelectionState.createEmpty(startKey).merge({
          anchorOffset: linkStart,
          focusOffset: linkEnd,
        });
        
        const newContentState = Modifier.applyEntity(
          contentState,
          linkSelection,
          null
        );
        
        const newEditorState = EditorState.push(
          editorState,
          newContentState,
          'apply-entity'
        );
        
        onEditorStateChange(newEditorState);
      }
    }
  };

  // æ£€æŸ¥å½“å‰é€‰æ‹©æ˜¯å¦åŒ…å«é“¾æŽ¥
  const hasLinkInSelection = () => {
    return getCurrentLinkEntity(editorState) !== null;
  };

  return {
    linkDialogOpen,
    setLinkDialogOpen,
    getCurrentLinkEntity,
    insertLink,
    updateLink,
    removeLink,
    hasLinkInSelection
  };
};